import React, { useState, } from 'react';
import { Collapse, Drawer, Button, Tooltip, Form, Input, InputNumber, Switch, Row, Col, Select, message, } from 'antd';
import { Icon, Selector } from "@/components";
import Apis from "@/apis";
const { Panel } = Collapse;
const labelCol = { span: 6 }
const comOptions = [
  { label: '', value: 'Input' },
  { label: '', value: 'Select' },
  { label: '', value: 'InputNumber' },
  { label: '', value: 'DatePicker' },
  { label: '', value: 'Switch' },
]

const AppView = (props, ref) => {
  const [items, setItems] = useState(props.items.map(item => {
    let _item = { ...item }
    if (_item.selectOptions && _item.selectOptions.id) {
      _item.selectOptions = _item.selectOptions.id
    }
    return _item
  }));
  const { comName, viewData, onUpdate, onClose, ...otherProps } = props

  const handleClick = (index, type, e) => {
    e.stopPropagation();
    let _items = items.splice(index, 1)
    if (type === 'up') {
      items.splice(index - 1 < 0 ? 0 : (index - 1), 0, ..._items)
    } else if (type === 'down') {
      items.splice(index + 1 > items.length ? items.length : (index + 1), 0, ..._items)
    }
    setItems(Array.from(items))
  }

  const genExtra = (index, items) => {
    return <span>
      {index ? <Tooltip placement="bottom" title={''} key="0">
        <Button type="link" size="small" icon={<Icon iconName="Up" />} onClick={e => handleClick(index, 'up', e)}></Button>
      </Tooltip> : null}
      {index < items.length - 1 ? <Tooltip placement="bottom" title={''} key="1">
        <Button type="link" size="small" icon={<Icon iconName="Down" />} onClick={e => handleClick(index, 'down', e)}></Button>
      </Tooltip> : null}
    </span>
  }

  const handleChange = (index, fieldName, e) => {
    let item = items[index]
    if (item) item[fieldName] = e ? (e.target ? e.target.value : e) : null
    setItems(Array.from(items))
  }

  const handleSave = () => {
    if (comName && viewData) {
      let component = viewData.components.find(item => item.__component === comName)
      if (component) {
        component.columns = items
        // console.warn(viewData);
        Apis.updateViewData(viewData).then(res => {
          if (res) {
            message.success("!");
            if (typeof onUpdate === 'function') onUpdate()
          } else {
            message.error("!");
          }
        })
      }
    }
  }

  const genItem = items => {
    return items.map((item, index) => (
      <Panel header={item.title} extra={genExtra(index, items)} key={index}>
        <Form.Item labelCol={labelCol} label="">
          <Input onChange={handleChange.bind(this, index, 'title')} value={item.title || ''} />
        </Form.Item>
        <Form.Item labelCol={labelCol} label="">
          <Selector
            api="customizeFuns"
            params={{ _limit: 1000, type_ne: 'form_data' }}
            titlepropname="title"
            valuepropname="id"
            grouppropname="type"
            showSearch
            searchname="title"
            filterOption={(input, option) =>
              option.children ? option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0 : false
            }
            allowClear
            onChange={handleChange.bind(this, index, 'render')}
            value={item.render ? item.render.id : undefined}
          />
        </Form.Item>
        {item.dataIndex ?
          <Row>
            <Col span={12}>
              <Form.Item labelCol={{ span: 12 }} label="">
                <Switch onChange={handleChange.bind(this, index, 'forSearch')}
                  checkedChildren="" unCheckedChildren=""
                  checked={item.forSearch}
                />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item labelCol={{ span: 12 }} label="">
                <Switch onChange={handleChange.bind(this, index, 'sortEnable')}
                  checkedChildren="" unCheckedChildren=""
                  checked={item.sortEnable} />
              </Form.Item>
            </Col>
          </Row> : null
        }
        {
          item.forSearch ?
            <Form.Item labelCol={labelCol} label="">
              <Select
                options={comOptions}
                onChange={handleChange.bind(this, index, 'component')}
                value={item.component}
              />
            </Form.Item> : null
        }
        {
          item.forSearch && item.component === 'Select' ?
            <Form.Item labelCol={labelCol} label="">
              <Selector
                api="dictionarys"
                params={{ _limit: 1000 }}
                value={item.selectOptions}
                titlepropname="title"
                valuepropname="_id"
                onChange={handleChange.bind(this, index, 'selectOptions')}
              />
            </Form.Item> : null
        }
        <Row>
          <Col span={12}>
            <Form.Item labelCol={{ span: 12 }} label="">
              <Switch onChange={handleChange.bind(this, index, 'isExtend')}
                checkedChildren="" unCheckedChildren=""
                checked={item.isExtend}
              />
            </Form.Item>
          </Col>
          <Col span={12}>
            {
              item.isExtend ?
                <Form.Item labelCol={{ span: 12 }} label="">
                  <Switch onChange={handleChange.bind(this, index, 'isPhoto')}
                    checkedChildren="" unCheckedChildren=""
                    checked={item.isPhoto} />
                </Form.Item> : null
            }
          </Col>
        </Row>
        {
          item.isPhoto ?
            <Form.Item labelCol={labelCol} label="">
              <InputNumber onChange={handleChange.bind(this, index, 'photoWidth')} value={item.photoWidth} />
            </Form.Item> : null
        }
      </Panel>
    ))
  }

  return (
    <Drawer
      title={''}
      placement="right"
      closable={true}
      width={350}
      zIndex={1000}
      bodyStyle={{ padding: 0 }}
      className="fieldSetting"
      onClose={onClose}
      footer={
        <div style={{ textAlign: 'center', }}>
          <Button onClick={handleSave} type="primary" style={{ marginRight: 8 }}>
            
          </Button>
          <Button onClick={e => onClose ? onClose() : null}>
            
          </Button>
        </div>
      }
      {...otherProps}
    >
      <Collapse
        bordered={false}
        accordion={true}
        defaultActiveKey={['']}
      >
        {genItem(items)}
      </Collapse>
    </Drawer>
  );
}
export default AppView